.heading-tertiary {
    letter-spacing: 0.5rem;
    font-size: $default-font-size;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
}

.paragraph {
    font-size: $default-font-size;

    &:not(:last-child) {
        margin-bottom: 3rem;
    }
}
.smallParagraph {
    font-size: $default-small-font-size;
}

.paragraphQuote {
    margin-top: 5rem;
    font-size: $default-quote-font-size;
}

.storyParagraph {
    font-size: $our-story-font-size;
}

.skills-primary {
    font-size: 3.5rem;
    text-transform: uppercase;
    font-weight: 700;
    display: inline-block;
    // background-image: linear-gradient(to right, $color-primary-light, $color-primary-dark);
    -webkit-background-clip: text;
    color: $color-white;
    border-bottom: 1px solid $color-white;
}

.bold {
    font-weight: bold;
}
